
@charset "utf-8";
@import "variables";
@import "mixin";

/* ==================================================
	main visual
================================================== */
.l-mainvisual {
	background-color: lighten(gray, 30);
	width: 100%;
	height: 400px;
	justify-content: center;
	align-items: center;
	margin-bottom: 30px;
	h2 {
		margin-bottom: 0;
	}
}

/* ==================================================
404
================================================== */
.p-404 {
	background-color: #efefef;
	padding: 100px 0 100px;
	text-align: center;
	font-size: 1.8rem;
	&__title {
		font-size: 3rem;
    line-height: 1.2;
    font-weight: bold;
		margin-bottom: 30px;
	}
	&__text {
		margin-bottom: 30px;
	}
	a {
		color: #fff;
    background: #333;
    display: flex;
    align-items: center;
    justify-content: center;
		text-align: center;
		width: 300px;
		height: 60px;
		margin: 0 auto;
		position: relative;
		z-index: 9;
		text-decoration: none;
		&:hover {
			opacity: 1;
			color: #333;
			&:before {
				animation: hover-in .3s forwards alternate;
			}
		}
		&:before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: -1;
			content: '';
			transform: scale(0, 1);
			transition: all .3s;
			background: #fff;
			border: 2px solid #333;
		}
	}
}
@keyframes hover-in {
  0% {
    transform-origin: left top;
    transform: scale(0, 1); }
  100% {
    transform-origin: left top;
		transform: scale(1, 1); } }

/* ==================================================
	お問い合わせ完了ページ（Thanks）
================================================== */
.p-thank {
	padding: 100px 0 100px;
	text-align: center;
	font-size: 1.8rem;
	figure {
		margin-bottom: 30px;
	}
}